/**
 * Format Date
 * 
 * @param d Date object
 # @return 'mm/dd/yyyy'
 */
const formatDate = (d: Date): string => {
  let month = `${  d.getMonth() + 1}`;
  let day = `${  d.getDate()}`;
  const year = d.getFullYear();

  if (month.length < 2)
    month = `0${  month}`;
  if (day.length < 2)
    day = `0${  day}`;

  return [month, day, year].join('/');
};

export {
  formatDate
};