/* eslint-disable import/no-extraneous-dependencies, @typescript-eslint/no-explicit-any */
import React from 'react';
import Tour from 'reactour';
import styled, { keyframes } from 'styled-components';
import { BulbOutlined } from '@ant-design/icons';
import { useToggle } from '../../../hooks/index';
import { 
  FireIcon, 
  PointerIcon, 
  SnowIcon,
  WaterIcon
} from '../../../components/icon/index';

// ================= Create Styled Components =================
const GuideBtn = styled(BulbOutlined)`
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 30px;
`;

const moveLeftAnimation = keyframes`
  0% {
    left: -30%;
  }
  100% {
    left: 30%;
  }
`;
const moveRightAnimation = keyframes`
  0% {
    left: 30%;
  }
  100% {
    left: -30%;
  }
`;
const DragRightIcon = styled(PointerIcon)`
  animation-name: ${moveLeftAnimation};
  left: -30%;
  color: #3260a8;
`;
const DragLeftIcon = styled(PointerIcon)`
  animation-name: ${moveRightAnimation};
  left: 30%;
  color: #3260a8;
`;
const StyledFireIcon = styled(FireIcon)`
  color: rgb(250, 42, 27);
  font-size: 30px;
`;
const StyledSnowIcon = styled(SnowIcon)`
  color: rgb(92, 138, 255);
  font-size: 30px;
`;

// ================= Specify Guide Steps =================
/**
 * @selector wapper className of your target
 * @content put your guide components here
 * @position |"top"|"bottom"|"left"|"right"|[number, number] 
 * 
 * visit https://reactour.js.org/ for more detail
 */
const steps = [
  {
    selector: '.temperature-slider-wapper',
    content: ({ inDOM }: { inDOM: any }) => (
      <div>
        <br />
        <StyledFireIcon />
        {inDOM && 'Drag right to increase temperature!'}
        <br />
        <DragRightIcon />
      </div>
    ),
    position: 'top' as 'top',
  },
  {
    selector: '.temperature-slider-wapper',
    content: ({ inDOM }: { inDOM: any }) => (
      <div>
        <br />
        <StyledSnowIcon />
        {inDOM && 'Drag left to decrease temperature!'}
        <br />
        <DragLeftIcon />
      </div>
    ),
    position: 'top' as 'top',
  },
  {
    selector: '.water-slider-wapper',
    content: ({ inDOM }: { inDOM: any }) => (
      <div>
        <br />
        <WaterIcon level="high" />
        {inDOM && 'Drag right to increase water volume!'}
        <br />
        <DragRightIcon />
      </div>
    ),
    position: 'top' as 'top',
  },
  {
    selector: '.water-slider-wapper',
    content: ({ inDOM }: { inDOM: any }) => (
      <div>
        <br />
        <WaterIcon level="off" />
        {inDOM && 'Drag left to decrease water volume!'}
        <br />
        <DragLeftIcon />
      </div>
    ),
    position: 'top' as 'top',
  }
];

function Guide() {
  const [isTourOpen, toggleTour] = useToggle(false);

  return (
    <>
      <Tour
        steps={steps}
        isOpen={isTourOpen}
        onRequestClose={toggleTour}
      />
      <GuideBtn
        onClick={toggleTour}
        className='tour-btn'
      />
    </>
  );
}

export default Guide;
