import { useState, useEffect } from 'react';

/**
 * This hook takes a start date and update the time every 1000 ms 
 *
 * @param currentDate 
 * @return realTimeDate 
 */
const useTimer = (currentDate: Date): Date => {
  const [date, setDate] = useState(currentDate);

  useEffect(() => {
    const timerID = setInterval(() => tick(), 1000);
    return function cleanup() {
      clearInterval(timerID);
    };
  });

  function tick() {
    setDate(new Date());
  }

  return date;
};

export default useTimer;